import './App.css';
import React, { Component } from 'react';
import { Button } from '@material-ui/core';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';

// the child component, Nabar, of the left compoent is for filtering and sorting
class Navbar extends Component {
  constructor(props) {
    super(props);
    // this.state = {id:1,name:"Pucky Janggu Baby",price:"$32.00",imageUrl:"popmart_1.jpg"};
  }
  // default category which is all category(box or single)
  filterByAll = () => {
    console.log('its in filter by category');
    this.props.filterByCategory('default');
  }
// fiter by category single 
  filterByCategory = () => {
    console.log('its in filter by category');
    this.props.filterByCategory('single');
  }
// filter by category box
  filterByCategoryBox = () => {
    console.log('its in filter by category');
    this.props.filterByCategory('box');
  }
// filter by tag winter
   filterByTagWinter= () => {
    this.props.filterByTag('winter');
  }
// filter by tag summer
  filterByTagSummer= () => {
    this.props.filterByTag('summer');
  }
// filter by tag spring
  filterByTagSpring= () => {
    this.props.filterByTag('spring');
  }
// filter by tag fall
  filterByTagFall= () => {
    this.props.filterByTag('fall');
  }
// filter by tag limited
  filterByTagLimited= () => {
    this.props.filterByTag('limited');
  }
  // default tag which is all tags
  filterByTagDefault=() => {
    this.props.filterByTag('default');
  }
  // calls the sorting function based on the value
  priceChange= (e) => {
    console.log(e.target.value);
    let value = e.target.value;
    // 'high' is if price from high to low
    if(value=='high'){
      this.props.sortByPriceHighToLow();
    // 'low' is if price from low to high
    }else if(value=='low'){
      this.props.sortByPriceLowToHigh();
    // 'default' is the default order
  }else if(value=='default'){
      this.props.sortByPriceDefault();
    }
  }

  render(){
    return (
        <div className='navcontent'>
            <div className='navDiv'>
                Sort By Price:
                {/* select to choose price display order*/}
                <Select 
                name="price" 
                onChange = {(e) => {this.priceChange(e)}}
                inputProps={{
                  name: 'age',
                  id: 'age-native-simple',
                }}
                defaultValue={this.props.price == 'low'?'low':(this.props.price == 'high'?'high':'default') }
                value={this.props.price == 'low'?'low':(this.props.price == 'high'?'high':'default') }
                >
                  <option 
                  className="option" value="low" 
                  >low to high</option>
                  <option className="option" value="high" 
                  >high to low</option>
                  <option className="option" value="default"
                  >default</option>
                </Select>          
            </div>

            <div className='navDiv'>
                {/* buttons to choose category */}
                <span className="leftTitle">Product:</span>
                <Button className='btn info' onClick = {this.filterByCategory}
                style = {this.props.category == 'single'?{'background-color':'#faefcf'}:{}}
                > single item </Button>
                <Button className='btn info' onClick = {this.filterByCategoryBox}
                style = {this.props.category == 'box'?{'background-color':'#faefcf'}:{}}
                > box item </Button>
                <Button className='btn info' onClick = {this.filterByAll}
                style = {this.props.category == 'default'?{'background-color':'#faefcf'}:{}}
                > all categories </Button>
            </div>

            <div className='navDiv'>
                {/* buttons to choose tag */}
                <span className="leftTitle">Collection:</span>
                <Button className='btn info' onClick = {this.filterByTagWinter}
                style = {this.props.tag == 'winter'?{'background-color':'#faefcf'}:{}}
                > winter items </Button>
                <Button className='btn info' onClick = {this.filterByTagSpring}
                style = {this.props.tag == 'spring'?{'background-color':'#faefcf'}:{}}
                > spring items </Button>
                <Button className='btn info' onClick = {this.filterByTagFall}
                style = {this.props.tag == 'fall'?{'background-color':'#faefcf'}:{}}
                > fall items </Button>
                <Button className='btn info' onClick = {this.filterByTagSummer}
                style = {this.props.tag == 'summer'?{'background-color':'#faefcf'}:{}}
                > summer items </Button>
                <Button className='btn info' onClick = {this.filterByTagLimited}
                style = {this.props.tag == 'limited'?{'background-color':'#faefcf'}:{}}
                > limited items </Button>
                <Button className='btn info' onClick = {this.filterByTagDefault}
                style = {this.props.tag == 'default'?{'background-color':'#faefcf'}:{}}
                > all tags </Button>
            </div>
            {/* button to reset all price sorting, tag filtering, and category filtering choices to default*/}
            <div className='navDiv' style={{marginLeft:'0'}}><Button className='btn info' onClick = {this.props.resetAll}>reset all</Button></div>
        </div>
    );
  }
}
export default Navbar;
