import './App.css';
import React, { Component } from 'react';
import { Button } from '@material-ui/core';
// the child component, card, of the left and the right compoent is for displaying, adding, or deleting((only in the right component, shopping cart)) items.
class Card extends Component {
  constructor(props) {
    super(props);
    // this.state = {id:1,name:"Pucky Janggu Baby",price:"$32.00",imageUrl:"popmart_1.jpg"};
  }
  render(){
    return (
        // checks and updates item in cart
        <div className={this.props.is_in_cart == 'no'?'card':'cardInCart'}>
            <div>
                {(()=> {
                    if (this.props.is_in_cart=='yes'){
                    return(<img id="imageRight" src={process.env.PUBLIC_URL + '/images/'+ this.props.imageUrl} width="100" height="80"/> )
                    }else{
                        return(<img id="imageLeft" style={{width:"120px",height:"150px"}} src={process.env.PUBLIC_URL + '/images/'+ this.props.imageUrl} /> )
                    }
                })()}
            </div>
            {/* item name */}
            <div>
                {this.props.name}
            </div>
            {/* item price */}
            <div>
                {'$' + this.props.price.toString()}
            </div>
            {/* item's category: single or box */}
            <div>
                {this.props.category}
            </div>
            {/* item's tag: winter,summer,fall, spring, or limited */}
            <div>
                {this.props.tag}
            </div>
            {(()=> {
                if (this.props.is_in_cart=='yes'){
                return(
                <div>
                    <div>
                        {/* button to add 1 from the item quantities in shopping cart*/}
                        <Button variant="contained" color="primary" size="small" onClick={() => {this.props.quantityDecrease(this.props.cardId)}}>-</Button>
                        <span>&nbsp;&nbsp;Quantities:{this.props.cardNum}&nbsp;&nbsp;</span>
                        {/* button to delete 1 from the item quantities in shopping cart */}
                        <Button variant="contained" color="primary" size="small" onClick={() => {this.props.quantityIncrease(this.props.cardId)}}>+</Button>
                    </div>
                </div>)
                }else{
                    return(
                        <div>
                        {/* "add to cart" button to add an item from the left component */}
                        <Button variant="contained" color="primary" size="small" onClick={()=>{this.props.addToCart(this.props.cardId)}}>Add To Cart</Button>
                        </div>)
                }
            })()}
        </div>
    )
  }
}
export default Card;
