import './App.css';
import React, { Component } from 'react';
import Card from './Card';
import Navbar from './Navbar';
import {BrowserRouter as Router,Switch,Route,Link} from 'react-router-dom';
import { Button } from '@material-ui/core';

// constance with 12 item data including price, image, catgeory, & tag. Add to the list if there's additional items to be displayed to users. 
const originalData = [
  {id:1,name:"Pucky Janggu Baby",price: 32.00,imageUrl:"popmart_1.jpg",category:"single",tag:"fall"},
  {id:2,name:"Bunny Christmas",price: 9.50,imageUrl:"popmart_2.jpg",category:"single",tag:"winter"},
  {id:3,name:"Bobo&Coco Set ",price: 114.00,imageUrl:"popmart_3.jpg",category:"box",tag:"summer"},
  {id:4,name:"The Monsters Set",price: 90.50,imageUrl:"popmart_4.jpg",category:"box",tag:"spring"},
  {id:5,name:"Mail Bobo",price: 12.50,imageUrl:"popmart_5.jpg",category:"single",tag:"summer"},
  {id:6,name:"Halloween Set",price: 100.50,imageUrl:"popmart_6.jpg",category:"box",tag:"fall"},
  {id:7,name:"Rabbit Boy",price: 12.50,imageUrl:"popmart_7.jpg",category:"single",tag:"spring"},
  {id:8,name:"Molly Leaf",price: 20.50,imageUrl:"popmart_8.jpg",category:"single",tag:"limited"},
  {id:9,name:"Winter Fantasy Set",price: 80.50,imageUrl:"popmart_9.jpg",category:"box",tag:"winter"},
  {id:10,name:"TV Pucky Set",price: 100.50,imageUrl:"popmart_10.jpg",category:"box",tag:"summer"},
  {id:11,name:"Skull Panda Set",price: 120.50,imageUrl:"popmart_11.jpg",category:"box",tag:"limited"},
  {id:12,name:"Santa Momoji",price: 9.50,imageUrl:"popmart_12.jpg",category:"single",tag:"winter"},
]

//  class component, App, that extend from React.Component and creates a render function which returns a React element
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: originalData,
      filterApplied: false,
      tagApplied: false,
      dataWithoutTag: originalData,
      dataWithoutCategory: originalData,
      dataOnlySort: originalData,
      shoppingCart: {},
      category: 'default',
      tag: 'default',
      price: 'default'
    };
  }
  // add item to shopping cart if item is not in cart; if item already in cart, add 1
  addToCart(item_id){
    if(!this.state.shoppingCart.hasOwnProperty(item_id)){
      this.state.shoppingCart[item_id]=1;
    }else{
      this.state.shoppingCart[item_id]+=1;
    }
    this.setState(this.state);

  }
// takes in the item's id and return's the item object
  searchDetail = (item_id) => {
    for (let item of originalData){
      if (item.id == item_id){
        return item;
      }
    }
  }
// sorts the price from low to high
  sortByPriceLowToHigh(){
    this.state.data.sort(function(a,b){return a.price - b.price});
    this.state.dataOnlySort.sort(function(a,b){return a.price - b.price});
    this.state.price='low';
    this.setState(this.state);
  }
// sorts the price from high to low
  sortByPriceHighToLow(){
    this.state.data.sort(function(a,b){return b.price - a.price});
    this.state.dataOnlySort.sort(function(a,b){return b.price - a.price});
    this.state.price='high';
    this.setState(this.state);
  }
  // sorts based on the default order
  sortByPriceDefault(){
    this.state.data.sort(function(a,b){return a.id - b.id});
    this.state.dataOnlySort.sort(function(a,b){return a.id - b.id});
    this.state.price='default';
    this.setState(this.state);
  }
  // takes in the category_name and filters the items based on the category(single or box)
  filterByCategory(category_name){
    if(category_name=='default'){
      this.state.category = category_name;
      this.state.data = this.state.dataOnlySort;
      if(this.state.tag!='default'){
        this.state.data = this.state.data.filter((item)=> {return item.tag == this.state.tag;})
      }
      this.setState(this.state);
    }
    else{
      this.state.data = this.state.dataOnlySort.filter(function (item){
        if(this.state.tag!='default'){
          return item.category == category_name && item.tag == this.state.tag;
        }
        return item.category == category_name;
      }.bind(this));
      this.state.category = category_name;
      this.setState(this.state);
    }
  }
// takes in the tag_name and filters the items based on the tag which is the collection name such as 'winter','summer','spring','fall', 'limited'
  filterByTag(tag_name){

    if(tag_name == 'default'){
      this.state.tag = tag_name;
      this.state.data = this.state.dataOnlySort;
      if(this.state.category!='default'){
        this.state.data = this.state.data.filter((item)=> {return item.category == this.state.category;})
      }
      this.setState(this.state);
    }else{
      this.state.data = this.state.dataOnlySort.filter(function(item){
        if(this.state.category!= 'default'){
          return item.tag == tag_name && item.category == this.state.category;
        }
        return item.tag == tag_name;
      }.bind(this));
      this.state.tag = tag_name;
      this.setState(this.state);

    }
  }
// resets all the sorting and filtering to 'default' 
  resetAll(){
    this.state.data = originalData;
    this.state.data.sort(function(a,b){return a.id - b.id});
    this.state.tag = 'default';
    this.state.price = 'default';
    this.state.category = 'default';
    this.setState(this.state);
  }
// takes in the item's id and increase the item's quantity by 1 in the shopping cart
  quantityIncrease(id){
    this.state.shoppingCart[id]+=1;
    this.setState(this.state);
  }
// when the item exists in the cart, takes in the item's id and decreases the item's quantity by 1 in the shopping cart only 
  quantityDecrease(id){
    if(this.state.shoppingCart[id]>0){
      this.state.shoppingCart[id]-=1;
      this.setState(this.state);
    }
  }

// checkout function that alerts a user the total cost of purchase based on items in the shopping cart
  checkout = () => {
    let current_total = Object.entries(this.state.shoppingCart).length>0?Object.entries(this.state.shoppingCart).map(function(a){
      return this.searchDetail(a[0]).price*a[1];
    }.bind(this)).reduce(function(a,b){
      return a+b;
    }):0;
    alert('Your total cost of purchasing is $' + current_total);
}
// renders the left(item card,sorting,filtering) and right components(shopping cart)
  render(){
    return (
      <div>
        {/* main section of page for displaying items,sorting items, and filtering items */}
        <div className='board'>
          <div className='title'>The Mystery Box Shop</div>
          {/* the left component's child component Navbar is for filtering and sorting */}
              <Navbar 
              resetAll = {this.resetAll.bind(this)}
              sortByPriceLowToHigh = {this.sortByPriceLowToHigh.bind(this)} 
              sortByPriceHighToLow = {this.sortByPriceHighToLow.bind(this)}
              sortByPriceDefault = {this.sortByPriceDefault.bind(this)} 
              filterByCategory = {this.filterByCategory.bind(this)}
              filterByTag = {this.filterByTag.bind(this)}
              price = {this.state.price}
              category = {this.state.category}
              tag = {this.state.tag}
              />    
              <div className="contentContainer">
              {this.state.data.map(function(key){
                return (
                <div style={{fontSize:"20px",marginTop:"10px"}}> 
            {/* the left component's other child component Card which is for displaying the 12 items */}
                  <Card 
                   is_in_cart='no'
                   addToCart={this.addToCart.bind(this)}
                   cardId={key.id} 
                   imageUrl= { key.imageUrl} 
                   name={ key.name } 
                   price={ key.price } 
                   category={key.category} 
                   tag={key.tag} />
                </div>
                        );
              }.bind(this))}
              </div>
        </div>
        {/* shopping cart */}
        <div className='ShoppingCart'>
          <div className='title2'>Shopping Cart:</div>
          <div className='shoppingitem'>
          {Object.entries(this.state.shoppingCart).filter((item)=>{return item[1]>0;}).map(function(entry){
            let current_item = this.searchDetail(entry[0]);
            console.log(entry);
                return (
                <div style={{fontSize:"20px",marginTop:"10px"}}> 
                 {/* the right component only has the card component for displaying the items added to the shopping cart */}
                  <Card 
                   quantityIncrease={this.quantityIncrease.bind(this)}
                   quantityDecrease={this.quantityDecrease.bind(this)}
                   is_in_cart='yes'
                   addToCart={this.addToCart.bind(this)}
                   cardId={entry[0]} 
                   cardNum={entry[1]}
                   imageUrl= {current_item.imageUrl} 
                   name={current_item.name} 
                   price={current_item.price} 
                   category={current_item.category} 
                   tag={current_item.tag} 
                   />
                </div>
                        );
              }.bind(this))}
          </div>
          {/* priceandbutton div shows the total price and checkout */}
              <div className='priceandbutton'>
                {/* total price of items added in cart */}
                <div className='cost'>
                  Total Cost: $
                  {Object.entries(this.state.shoppingCart).length>0?Object.entries(this.state.shoppingCart).map(function(a){
                    return this.searchDetail(a[0]).price*a[1];
                  }.bind(this)).reduce(function(a,b){
                    return a+b;
                  }):0}
                </div>
                {/* checkout button */}
                <div className = 'checkout'>
                  <Button variant="contained" color="primary" size="medium" onClick={this.checkout}>Checkout</Button>
                </div>              
              </div>
        </div>
      </div>
    );
  }
}
function AppWrapper(){
  return (
    <div>
    <Router>
      <Switch>
        <Route exact path = '/' render = {(props)=> {return (<App {...props}/>) }}/>
        {/* <Route exact path = '/ShoppingCart' render = {(props)=> {return (<ShoppingCart {...props}/>) }}/> */}
      </Switch>
    </Router>
    </div>
  )
}
export default AppWrapper;
